





















import Vue from "vue";
import { uuid } from "vue-uuid";

export default Vue.extend({
    props: {
        id: {
            type: String,
            required: false,
            default() {
                return `input-${uuid.v1()}`;
            }
        },
        type: {
            type: String as () => `text`,
            required: true
        },
        value: {
            type: String,
            required: true
        },
        label: {
            type: String,
            required: true
        },
        min: {
            type: Number,
            required: false
        },
        step: {
            type: Number,
            required: false
        },
        error: {
            type: String,
            required: false
        }
    },
    methods: {
        updateInput($event: Event): void {
            if ($event.target instanceof HTMLInputElement) {
                this.$emit("input", $event.target.value);
            }
        }
    }
});
